$(document).ready(function(){


    // Переменные блоков
    var header = $(".header");
    var content = $(".content");
    var sidebar = $(".sidebar");

    var medium_screen = 1024;

    /* */
    // function toggleCollapsed(block,speed,callback){
    // block.animate(
    //     {width: "0px"},
    //     speed, 
    //     function(){
    //     callback();
    //     });
    // }

    /* */
    // function toggleMobile(block){
    //
    // }
    /* Установить ширину */
    function setWidth(block,new_width,speed){
        // if(block.width() != new_width){
        //     block.clearQueue();
        //     block.animate(
        //         {opacity: 0},
        //         100,function () {
        //             block.animate(
        //                 {width: new_width,
        //                     opacity: 1},
        //                 speed,function () {
        //                     changeHeight();// Изменяем высоту после анимации
        //                 });
        //         });
        // }
        block.width(new_width);
        changeHeight();
    }
    /* Функция изменение высоты */
    function changeHeight(){
        var content_height = content.height(); // Высота блока контент
        var sidebar_height = sidebar.height(); // Высота блока контент
        sidebar.height(content_height);
    }

    /* Функция изменение ширины */
    function changeWidth() {
        var window_width = $( window ).width(); //Ширина окна

        // Меняем сайт на мобильную версию
        if(window_width <= medium_screen){
            header.addClass("mobile collapsed");
            sidebar.addClass("mobile collapsed");
        }
        else {
            header.removeClass("mobile collapsed");
            sidebar.removeClass("mobile collapsed");
        }

        // Состояния блоков
        var header_collapsed = ($('.header').hasClass('collapsed')) ? 0 : 1;
        var sidebar_collapsed = ($('.sidebar').hasClass('collapsed')) ? 0 : 1;

        var header_width = 250; // Ширина блока хедер
        var sidebar_width = 300; // Ширина блока сайдбар
        var content_width = window_width - ((header_width * header_collapsed) + (sidebar_width * sidebar_collapsed)); // Ширина блока контент (окно-(хедер+сайдбар))

        // Установка ширины
        setWidth(header,header_width*header_collapsed,0);
        setWidth(content,content_width,0);
        setWidth(sidebar,sidebar_width*sidebar_collapsed,0);
    }
    // Делаем разметку при загрузке
    changeWidth();
    // Делаем разметку при resize
    $( window ).resize(function() {
        changeWidth();
    });
    // Делаем разметку страницы и убираем прелодер после загрузки страницы
    $(window).on('load', function () {
        changeWidth();
        var $preloader = $('.preloader-back');
        $preloader.delay(100).fadeOut(1000);
        //Слайдер должен быть внизу из за того что ширина не успевает иногда просчитаться! ЭХ АСИНХРОННОСТЬ!
        $('.bxslider').bxSlider({
            mode: "fade",
            adaptiveHeight: true,
            controls: false,
            pager: true,
            captions: true,
            autoHover: true,
            speed: 1500,
            pause: 4000,
            nextText: '',
            prevText: ''
        });
    });

});
